import axios from './axios';

// 
export function getNewsList(option) {
  const url = '/weixin/news/lists';

  return axios.post(url, option);
}

// 
export function getNewsDetail(option) {
  const url = '/weixin/news/detail';

  return axios.post(url, option);
}

// 
export function getNewsPreview(option) {
  const url = '/weixin/news/previewDetail';

  return axios.post(url, option);
}

// 
export function getNewsComment(option) {
  const url = '/weixin/news/readComment';

  return axios.post(url, option);
}

// 
export function sendNewsComment(option) {
  const url = '/weixin/news/comment';

  return axios.post(url, option);
}

// 
export function getNewsReply(option) {
  const url = '/weixin/news/readReply';

  return axios.post(url, option);
}

// 
export function sendNewsReply(option) {
  const url = '/weixin/news/reply';

  return axios.post(url, option);
}



